import React, { useState, useEffect, useRef } from 'react';
import styles from './index.less';

// 轮播图数据
const carouselData = [
  {
    url: require('@/static/images/carousel/0V4A1307.jpg'),
    title: '深圳优莱克科技有限公司',
    description: '专注于为企业提供设计制造集成支持，PCBA电路板研发设计、SMT贴片焊接、DIP插件焊接、测试服务、成品组装、塑胶模具设计与制作的全套一站式电子产品解决方案 '
  },
  {
    url: require('@/static/images/carousel/0V4A13771.jpg'),
    title: '电子产品设计制造集成服务商',
    description: '采用先进的SMT设备，提供高精度贴装、BGA/CSP工艺，确保产品质量稳定可靠'
  },
  {
    url: require('@/static/images/carousel/0V4A1430.jpg'),
    title: '一站式电子产品解决方案',
    description: '从设计到量产，为客户提供完整的电子产品制造服务，包括硬件开发、方案优化、产品原型开发与功能验证'
  },

  {
    url: require('@/static/images/carousel/0V4A1488.jpg'),
    title: '快速交付能力',
    description: '双面快件24小时内完成，多层快件可在2-4天内完成，为客户提供快速响应服务'
  }
];

const Carousel = ({ autoPlay = true, interval = 3000 }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isTransitioning, setIsTransitioning] = useState(false);
  const intervalRef = useRef(null);

  const nextSlide = () => {
    if (isTransitioning) return;
    setIsTransitioning(true);
    setCurrentIndex((prevIndex) => (prevIndex + 1) % carouselData.length);
  };

  const prevSlide = () => {
    if (isTransitioning) return;
    setIsTransitioning(true);
    setCurrentIndex((prevIndex) => (prevIndex - 1 + carouselData.length) % carouselData.length);
  };

  const goToSlide = (index) => {
    if (isTransitioning || index === currentIndex) return;
    setIsTransitioning(true);
    setCurrentIndex(index);
  };

  useEffect(() => {
    if (autoPlay) {
      intervalRef.current = setInterval(nextSlide, interval);
    }
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [autoPlay, interval, currentIndex]);

  useEffect(() => {
    const timer = setTimeout(() => {
      setIsTransitioning(false);
    }, 300);
    return () => clearTimeout(timer);
  }, [currentIndex]);

  return (
    <div className={styles.carousel}>
      <div className={styles.carouselContainer}>
        {carouselData.map((image, index) => (
          <div
            key={index}
            className={`${styles.slide} ${index === currentIndex ? styles.active : ''}`}
            style={{
              backgroundImage: `url(${image.url})`,
            }}
          >
            <div className={styles.slideContent}>
              <div className={styles.slideText}>
                <h2>{image.title}</h2>
                <p>{image.description}</p>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* 左右箭头 */}
      <button className={`${styles.arrow} ${styles.prev}`} onClick={prevSlide}>
        ‹
      </button>
      <button className={`${styles.arrow} ${styles.next}`} onClick={nextSlide}>
        ›
      </button>

      {/* 指示器 */}
      <div className={styles.indicators}>
        {carouselData.map((_, index) => (
          <button
            key={index}
            className={`${styles.indicator} ${index === currentIndex ? styles.active : ''}`}
            onClick={() => goToSlide(index)}
          />
        ))}
      </div>
    </div>
  );
};

export default Carousel;
